=================================================*/
// $main-font-family: 'Open Sans', sans-serif;
$main-font-family: 'Ubuntu', sans-serif;
// $optional-font-family: 'Dosis', sans-serif;
$optional-font-family: 'Comfortaa', sans-serif;
// $main-color: #ff4800;
$main-color:#FD0052;
$optional-color: #4ac728;
$white-color: #ffffff;
$black-color: #333333;
$paragraph-color: #57647c;
$color-accent: #FD0052;
$indigo-color: #37005E;
$font-size: 14px;
$transition: .5s;

body {
    padding: 0;
    margin: 0;
    font: {
        family: $main-font-family;
        size: $font-size;
    }
}
a {
    display: block;
    transition: $transition;
    text-decoration: none;
    color: $black-color;

    &:hover, &:focus {
        text-decoration: none;
        color: $main-color;
    }
}
button {
    outline: 0 !important;
}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    color: $indigo-color;
    font: {
        family: $optional-font-family;
    }
}
.d-table {
    width: 100%;
    height: 100%;

    &-cell {
        vertical-align: middle;
    }
}
p {
    font-size: $font-size;
    color: $paragraph-color;
    margin-bottom: 15px;
    line-height: 1.8;

    &:last-child {
        margin-bottom: 0;
    }
}
.ptb-110 {
    padding: {
        top: 100px;
        bottom: 100px;
    }
}
img {
    max-width: 100%;
}
.bg-f2f6f9 {
    background-color: #f2f6f9;
}
.bg-fafafa {
    background-color: #fafafa;
}
/*section-title*/
.section-title {
    text-align: center;
    max-width: 670px;
    margin: {
        left: auto;
        right: auto;
        bottom: 60px;
        top: -6px;
    }
    span {
        display: block;
        color: $main-color;
        text-transform: uppercase;
        margin-bottom: 5px;
        font: {
            weight: 600;
            size: 15px;
            family: $optional-font-family;
        }
    }
    h2 {
        margin-bottom: 0;
        font: {
            size: 40px;
            weight: 700;
        }
    }
    p {
        max-width: 520px;
        margin: {
            bottom: 0;
            top: 10px;
            left: auto;
            right: auto;
        }
    }
}
/*btn-primary*/
.btn {
    border: none;
    line-height: initial;
    border-radius: 30px;
    transition: $transition;
    padding: 10px 32px 12px;
    position: relative;
    z-index: 1;
    opacity: 1 !important;
    font: {
        size: 16px;
        family: $optional-font-family;
        weight: 600;
    }
}
.btn-primary {
    color: $white-color;
    background-color: $main-color;

    &.disabled, &:disabled {
        color: $white-color;
        background-color: $main-color;
    }
    &:hover, &:focus {
        background-color: $optional-color !important;
        color: $white-color !important;
        box-shadow:  unset !important;
    }
}
/*learn-more-btn*/
.learn-more-btn {
    display: inline-block;
    position: relative;
    color: $main-color;
    letter-spacing: .5px;
    font: {
        size: 15px;
        weight: 600;
        family: $optional-font-family;
    }
    i {
        margin-left: 2px;
        position: relative;
        top: -0.6px;

        &::before {
            font-size: 11px;
        }
    }
    &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 0;
        background: $main-color;
        height: 1px;
        opacity: 0;
        visibility: hidden;
        transition: $transition;
    }
    &:hover {
        color: $main-color;

        &::before {
            width: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}
/*form-control*/
.form-control {
    height: 45px;
    border: 1px solid #eeeeee;
    background-color: transparent;
    border-radius: 0;
    transition: $transition;
    color: $black-color;
    line-height: initial;
    padding-left: 13px;
    font: {
        size: 14px;
    }
    &:hover, &:focus {
        box-shadow: unset;
    }
    &::placeholder {
        color: #999999;
    }
    &:focus {
        box-shadow: unset;
        border-color: $main-color;
    }
}

/*================================================
Preloader Area CSS
=================================================*/
.preloader {
    position: fixed;
    left: 0;
    width: 0;
    height: 100%;
    width: 100%;
    text-align: center;
    z-index: 9999999;
    transition: .9s;

    .loader {
        position: absolute;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        display: inline-block;
        left: 0;
        right: 0;
        margin: 0 auto;
        top: 45%;
        transform: translateY(-45%);
        transition: $transition;

        .loader-outter {
            position: absolute;
            border: 4px solid $white-color;
            border-left-color: transparent;
            border-bottom: 0;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            animation: loader-outter 1s cubic-bezier(.42, .61, .58, .41) infinite;
        }
        .loader-inner {
            position: absolute;
            border: 4px solid $white-color;
            border-radius: 50%;
            width: 40px;
            height: 40px;
            left: calc(50% - 20px);
            top: calc(50% - 20px);
            border-right: 0;
            border-top-color: transparent;
            animation: loader-inner 1s cubic-bezier(.42, .61, .58, .41) infinite;
        }
    }
    &::before, &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 60%;
        z-index: -1;
        background: $main-color;
        transition: .9s;
    }
    &::after {
        top: auto;
        bottom: 0;
    }
    &.preloader-deactivate {
        visibility: hidden;

        &::after, &::before {
            height: 0;
        }
        .loader {
            opacity: 0;
            visibility: hidden;
        }
    }
}
@keyframes loader-outter {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
@keyframes loader-inner {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(-360deg);
    }
}

