    animation-duration: 20s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}
.shape-img7 {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    opacity: .20;
    animation: rotate3d 4s linear infinite;
}
.shape-img3 {
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
    opacity: .10;
    animation: rotate3d 4s linear infinite;
}
.shape-img4 {
    position: absolute;
    right: 40%;
    bottom: 8%;
    z-index: -1;
    animation: animationFramesOne 20s infinite linear;
}
.shape-img5 {
    position: absolute;
    right: 5%;
    top: 50%;
    transform: translateY(-50%);
    z-index: -1;
