        list-style-type: none;
        padding-left: 0;
        margin: {
            bottom: 0;
        }
        .accordion-item {
            display: block;
            box-shadow: 0 0 1.25rem rgba(108, 118, 134, 0.1);
            background: $white-color;
            margin-bottom: 15px;
            border: 0;

            &:last-child {
                margin-bottom: 0;
            }
            &.active {
                .accordion-content {
                    display: block;
                }
                .accordion-title {
                    border-bottom-color: #eeeeee;

                    i {
                        &::before {
                            content: "\f068";
                        }
                    }
                }
            }
        }
        .accordion-title {
            padding: 12px 20px 12px 51px;
            color: $black-color;
