                    right: 7px;
                }
            }
        }
        textarea.form-control {
            height: auto;
            padding-top: 15px;
        }
        .btn {
            padding: 13px 35px 15px;
            font-size: 17px;
            margin-top: 8px;
            box-shadow: 0px 5px 28.5px 1.5px rgba(149, 152, 200, 0.2);

            &.disabled {
                cursor: not-allowed !important;

                &:hover, &:focus {
                    &::before {
                        opacity: 1;
